// libs
import React, { useState } from "react";
import { useHistory } from "react-router-dom";

// widgets
import ButtonStatus from "components/ButtonStatus";
import CheckBoxBody from "components/MultiCheckBox/CheckBoxBody";
import CheckBoxHeader from "components/MultiCheckBox/CheckBoxHeader";
import SortIcon from "components/SortIcon";

// assets
import { Images } from "shared/Images";

// consts
import { ADMIN_BASE_ROUTE, STRINGS } from "shared/Constants";
import { getPaginatedIndex } from "shared/Utilities";

const TableView = ({
    data = [],
    multipleSelectedData = [],
    setMultipleSelectedData,
    toggleDeleteModal,
    tableHeadings = [],
    sortData = {},
    allDataSelected = false,
    itemsPerPage = 15,
    pageNumber = 1,
}) => {
    const history = useHistory();
    const [isSelectAll, setIsSelectAll] = useState(allDataSelected || false);
    return (
        <>
            <thead>
                <tr>
                    <th>
                        <CheckBoxHeader
                            isSelectAll={allDataSelected || isSelectAll}
                            setMultipleSelectedData={setMultipleSelectedData}
                            setIsSelectAll={setIsSelectAll}
                            data={data}
                            multipleSelectedData={multipleSelectedData}
                            disabled={!data?.length}
                        />
                    </th>
                    {tableHeadings.map((i, idx) => (
                        <th scope="col" key={idx}>
                            <span> {i.title} </span>
                            {i.sortKey && (
                                <SortIcon
                                    toggleIcon={sortData[i.sortKey]}
                                    onClick={() => i.action(i.sortKey, sortData[i.sortKey])}
                                />
                            )}
                        </th>
                    ))}
                </tr>
            </thead>

            {data?.length ? (
                data.map((value, index) => (
                    <tbody key={index}>
                        <tr>
                            <td width={80}>
                                <CheckBoxBody
                                    setMultipleSelectedData={setMultipleSelectedData}
                                    setIsSelectAll={setIsSelectAll}
                                    value={value}
                                    multipleSelectedData={multipleSelectedData}
                                />
                            </td>
                            <td>{getPaginatedIndex(itemsPerPage,pageNumber,index)}</td>
                            <td>{value.group_name}</td>
                            <td>{value.group_discription ? `${value.group_discription}` : 'N/A'}</td>
                            <td width={200}>
                                <div className="d-flex btn_group">
                                    <ButtonStatus
                                        image={Images.EyeIcon}
                                        title={"View"}
                                        onClick={() => {
                                            history.push({ pathname: `${ADMIN_BASE_ROUTE}/view-group/${value?.id}`})
                                        }}
                                    />
                                    <ButtonStatus
                                        image={Images.EditIcon}
                                        title={"Edit"}
                                        onClick={() => {
                                            history.push({ pathname: `${ADMIN_BASE_ROUTE}/edit-group/${value?.id}` })
                                        }}
                                    />
                                    <ButtonStatus
                                        image={Images.TrashIcon}
                                        title={"Delete"}
                                        status={"danger"}
                                        onClick={() => {
                                            toggleDeleteModal(value)
                                        }}
                                    />
                                </div>
                            </td>
                        </tr>
                    </tbody>
                ))
            ) : (
                <tbody>
                    <tr>
                        <td colSpan={6} className="text-center py-5">
                            {STRINGS.NO_DATA_AVAILABLE}
                        </td>
                    </tr>
                </tbody>
            )}
        </>
    );
};

export default TableView;
