import BreadCrumb from "components/BreadCrumb";
import React, { useCallback, useEffect, useState } from "react";
import {
    ADMIN_ROUTES,
    CANDIDATE_LIST_SORTING_KEYS,
    CANDIDATE_TABLE_VIEW,
    CANDIDATE_TAB_VIEW,
    SNACK_MESSAGES,
    SNACK_VARIANT,
    SORTKEYS,
    STRINGS,
    VACANCY_SORTING_KEYS,
    VACANCY_STATUS,
    VACANCY_TABLE_VIEW,
    VACANCY_TAB_VIEW,
} from "shared/Constants";

import useNotification from "hooks/Notifications";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import SearchBar from "components/SearchBar";
import { Images } from "shared/Images";
import { Pagination } from "components/Pagination";
import DeleteModal from "components/DeleteModal";
import { FILTERS_ENUM } from "views/authenticated/LocumAdmin/JobList/constants";
import CommonTable from "components/CommonTable";
import Filters from "components/Filter";
import TableView from "./TableView";
import NavTab from "components/TabNav";
import CustomButton from "components/CustomButton";
import { deleteVacancies, getVacancyList } from "redux/Actions/PharmacyAdminDashboard";
import moment from "moment";
import { TAB_VIEW, VACANCY } from "../constants";

function VacancyListing() {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotification] = useNotification();
    const [multipleSelectedData, setMultipleSelectedData] = useState([]);
    const [data, setData] = useState([]);
    const [totalItems, setTotalItems] = useState(0);
    const [itemsPerPage, setItemsPerPage] = useState(15);
    const [activePage, setActivePage] = useState(1);
    const [keyword, setKeyword] = useState(null);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const [filterPayload, setFilterPayload] = useState({});
    const [deleteData, setDeleteData] = useState({});
    const [sort, setSort] = useState({
        key: "",
        direction: SORTKEYS["ascending"],
    });
    const [toggleFilter, setToggleFilter] = useState(false);
    const [activeTab, setActiveTab] = useState(CANDIDATE_TAB_VIEW.APPLIED.ID);
    const [filterData, setFilterData] = useState({
        country: "",
        state: "",
        selectedDate: {
            from: null,
            to: new Date(""),
        },
        rateRange: [0, 0],
    });
    console.log(sort, "sortsortsortsort");
    const FILTERS_DATA = [
        {
            title: "Country",
            data: [],
            id: FILTERS_ENUM.branch,
            handleChange: (value) => {
                handleChangeFilterData("country", value);
            },
        },
        {
            title: "State",
            data: [],
            handleChange: (value) => {
                handleChangeFilterData("state", value);
            },
        },
        {
            title: "End Job Date",
            data: [],
            handleChange: (value) => {
                handleChangeFilterData("selectedDate", value);
            },
        },
    ];

    const tableHeadings = [
        {
            title: CANDIDATE_TABLE_VIEW.CANDIDATE_NAME,
            action: (key, value) => {
                handleSortData(key, value);
            },
            sortKey: CANDIDATE_LIST_SORTING_KEYS.CANDIDATE_NAME,
        },
        {
            title: CANDIDATE_TABLE_VIEW.APPLIED_DATE,
            action: (key, value) => {
                handleSortData(key, value);
            },
            sortKey: CANDIDATE_LIST_SORTING_KEYS.START_DATE,
        },
        {
            title: CANDIDATE_TABLE_VIEW.BRANCH,
        },
        {
            title: CANDIDATE_TABLE_VIEW.RATE,
        },
        {
            title: VACANCY_TABLE_VIEW.END_DATE,
            action: (key, value) => {
                handleSortData(key, value);
            },
            sortKey: CANDIDATE_LIST_SORTING_KEYS.END_DATE,
        },
        { title: VACANCY_TABLE_VIEW.ACTION, action: () => {} },
    ];

    console.log(tableHeadings, "tableHeadingstableHeadings");

    const handleChangeFilterData = (key, value) => {
        setFilterData((prevFilterData) => ({ ...prevFilterData, [key]: value }));
    };

    const resetFilter = () => {
        fetchData({ isResetting: true });
        setFilterData({
            country: "",
            state: "",
            selectedDate: {
                from: null,
                to: new Date(""),
            },
            rateRange: [0, 0],
        });
    };

    const fetchData = useCallback(
        ({
            currentPageForPayload = activePage,
            itemsCountPerPageForPayload = itemsPerPage,
            sorting = sort.key ? 7 : "",
            sort_type = sort.direction,
            status = activeTab,
            isResetting = false,
        } = {}) => {
            let payload = {
                page: currentPageForPayload,
                search: keyword ? keyword : "",
                limit: itemsCountPerPageForPayload,
                sorting: String(sorting) ? sorting : "",
                sort_type: String(sort_type),
                status: String(status || activeTab),
                country: isResetting ? "" : filterData.country,
                state: isResetting ? "" : filterData.state,
                start_date: isResetting
                    ? ""
                    : filterData.selectedDate.from
                    ? moment(filterData.selectedDate.from).format("YYYY-MM-DD")
                    : "",
                end_date: isResetting
                    ? ""
                    : filterData.selectedDate.to
                    ? moment(filterData.selectedDate.to).format("YYYY-MM-DD")
                    : "",
                start_rate: isResetting ? "" : filterData.rateRange[0] ?? "",
                end_rate: isResetting ? "" : filterData.rateRange[1] ?? "",
            };
            console.log(payload, "payload");
            // dispatch(
            //     getVacancyList(
            //         payload,
            //         (res, type) => {
            //             setData(res.data);
            //             setTotalItems(res.count);
            //         },
            //         (err) => {
            //             sendNotification({
            //                 msg: err?.data?.message
            //                     ? err.data.message
            //                     : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
            //                 variant: SNACK_VARIANT.ERROR,
            //             });
            //         },
            //     ),
            // );
        },
        [dispatch, itemsPerPage, activePage, keyword, sendNotification, activeTab, filterData],
    );

    const handleDelete = () => {
        console.log(deleteData, "delete branch data", multipleSelectedData);
        let arr = [];
        if (multipleSelectedData.length && !deleteData.id) {
            arr = multipleSelectedData;
        } else {
            arr = [deleteData.id];
        }
        let payload = {
            vacancies: { vacancy_list: arr },
        };
        console.log(payload, "delete branch data");
        dispatch(
            deleteVacancies(
                payload,
                (response) => {
                    fetchData();
                    sendNotification({
                        msg: response?.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                    toggleDeleteModal();
                },
                (err) => {
                    sendNotification({
                        msg: err ? err : SNACK_MESSAGES.OOPS_SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                    toggleDeleteModal();
                },
            ),
        );
    };

    const toggleDeleteModal = (value) => {
        setShowDeleteModal((prevState) => !prevState);
        console.log(value, showDeleteModal);
        if (value && !showDeleteModal) {
            setDeleteData(value);
        } else {
            setDeleteData({});
        }
    };

    const restPageCountAndCallAPI = (itemsCountPerPageForPayload = itemsPerPage) => {
        fetchData({ currentPageForPayload: 1, itemsCountPerPageForPayload });
        setActivePage(1);
    };

    const handleSearchData = () => {
        //setting active page to 1st page on search same doing for calling API
        restPageCountAndCallAPI();
    };

    const handleChangeKeyword = (e) => {
        setKeyword(e.target.value);
    };

    const handleItemsPerPageChange = (i) => {
        setItemsPerPage(i);
        restPageCountAndCallAPI(i);
    };

    const handlePageChange = (i) => {
        setActivePage(i?.selected + 1);
        fetchData({ currentPageForPayload: i?.selected + 1 });
    };

    const handleSortData = (key, value) => {
        setSort({ key: key, direction: value });
        console.log(key, value, "directiondirection");
        fetchData({ sorting: key, sort_type: value });
        setActivePage(1);
    };

    const onViewClick = (value) => {
        history.push(`${ADMIN_ROUTES.VIEW_CANDIDATE.slice(0, -4)}/${value.id}`);
    };

    const onEditClick = (value) => {
        history.push(`${ADMIN_ROUTES.EDIT_VACANCY.slice(0, -4)}/${value.id}`);
    };

    const handleAddNewVacancyClick = () => {
        history.push(ADMIN_ROUTES.CREATE_VACANCY);
    };

    const handleChangeTab = (id) => {
        setActiveTab(id);
        fetchData({ status: String(id), currentPageForPayload: 1 });
        setActivePage(1);
    };

    const handleFetchDataForFilter = () => {
        fetchData({ currentPageForPayload: 1 });
        setActivePage(1);
    };

    useEffect(() => {
        fetchData();
    }, []);

    return (
        <>
            <div className="d-flex align-items-center flex-wrap position-relative justify-content-between">
                <BreadCrumb
                    onClick={() => history.push(ADMIN_ROUTES.VACANCIES)}
                    previousLink={VACANCY.VACANCIES}
                    currentLink={VACANCY.CANDIDATE_LIST}
                />
                <div className="d-flex admin_action flex-wrap">
                    {multipleSelectedData.length ? (
                        <>
                            <button
                                className="btn btn-sm btn-danger ms-2"
                                onClick={() => {
                                    toggleDeleteModal();
                                }}
                            >
                                {STRINGS.DELETE}
                            </button>
                        </>
                    ) : null}
                    <SearchBar
                        searchAPI={handleSearchData}
                        keyword={keyword}
                        handleChangeKeyword={handleChangeKeyword}
                    />
                    {/* <CustomButton
                        className="ms-2 btn btn-sm btn-secondary"
                        type="button"
                        title={STRINGS.ADD_VACACNCY}
                        onClick={handleAddNewVacancyClick}
                    /> */}
                    <button
                        className="btn btn-primary ms-2"
                        onClick={() => {
                            setToggleFilter((prev) => !prev);
                            if (toggleFilter) {
                                resetFilter();
                            }
                        }}
                    >
                        <img
                            src={toggleFilter ? Images.CloseWhiteIcon : Images.FilterIcon}
                            alt="filter"
                        />
                    </button>
                </div>
            </div>

            <Filters
                toggle={toggleFilter}
                filterData={filterData}
                reset={resetFilter}
                handleChangeFilterData={handleChangeFilterData}
                handleFetchDataForFilter={handleFetchDataForFilter}
            />
            <hr className="divider mb-0" />

            <NavTab onClick={handleChangeTab} tabsData={TAB_VIEW} activeTab={activeTab} />
            <hr className="divider my-0" />
            <div className="tab_content pt-2">
                <CommonTable>
                    <TableView
                        data={data}
                        activeTab={activeTab}
                        multipleSelectedData={multipleSelectedData}
                        setMultipleSelectedData={setMultipleSelectedData}
                        toggleDeleteModal={toggleDeleteModal}
                        sortData={sort}
                        tableHeadings={tableHeadings}
                        allDataSelected={
                            multipleSelectedData?.length
                                ? multipleSelectedData?.length == data?.length
                                : false
                        }
                        onViewClick={onViewClick}
                        onEditClick={onEditClick}
                        itemsPerPage={itemsPerPage}
                        pageNumber={activePage}
                    />
                </CommonTable>

                {data?.length ? (
                    <Pagination
                        itemsCount={totalItems}
                        itemsPerPage={itemsPerPage}
                        handlePageChange={handlePageChange}
                        handleItemsPerPageChange={handleItemsPerPageChange}
                        activePage={activePage}
                    />
                ) : null}
            </div>

            <DeleteModal
                show={showDeleteModal}
                toggleModal={toggleDeleteModal}
                onClick={handleDelete}
                medium={true}
            />
        </>
    );
}

export default VacancyListing;
